                    <a-button type="primary" disabled>挑战</a-button>
                </template>
            </template>
        </a-table>
    </div>
</template>

<script>
import { reactive, toRefs, onMounted, ref } from "vue";
import rankApi from "@/assets/api/rankApi";
import { useI18n } from "vue-i18n";

export default {
    name: "Rank", // 排行
    setup() {
        const { t } = useI18n();
        const state = reactive({
            rankList: [],
            columns: [
                {
                    title: "排名",
                    dataIndex: "ranking",
                    key: "ranking",
                },
                {
                    title: "昵称",
                    dataIndex: "nickname",
                    key: "nickname",
                },
                {
                    title: "总灵气",
                    dataIndex: "attrTotal",
                    key: "attrTotal",
                },
                {
                    title: "基础灵气",
                    dataIndex: "attackpoint",
                    key: "attackpoint",
                },
                {
                    title: "额外灵气",
                    dataIndex: "extraTotal",
                    key: "extraTotal",
                },
                {
                    title: "积分",
                    key: "rankpoint",
                },
                {
                    title: "操作",
                    key: "action",
                },
            ],
        });

        onMounted(async () => {
            await getRankList();
        });

        const getRankList = async () => {
            let res = await rankApi.getList({});
            state.rankList = res.data;
        };

        return {
            ...toRefs(state),
        };
    },
};
